<template>
  <div class="order">
    <van-nav-bar title="" />
    <van-tabs @click="tabClickHandler" v-model="active" color="#1659a0">
      <van-tab title="">
      </van-tab>
      <van-tab title="">
      </van-tab>
      <van-tab title="">
      </van-tab> 
      <van-tab title="">
      </van-tab>
    </van-tabs>
    <briup-order-item v-for="item in orders" :data='item' :key="item.id"></briup-order-item>
  </div>
</template>
<script>
import { mapActions, mapState ,mapGetters} from 'vuex'
import {get} from '../../http/axios'
export default {
  data(){
    return {
      active:0,
      orders:[],
      status:null
    }
  },
  computed:{
    ...mapState("user",["info"])
  },
  created(){
    this.loadOrders()
  },
  methods:{
    tabClickHandler(name,title){
      // datastatus 
      this.status = title === "" ? null : title;
      this.loadOrders();
    },
    loadOrders(){
      let url = "/order/query";
      let params = {
        customerId:this.info.id,
        status:this.status
      }
      get(url,params).then((response)=>{
        this.orders = response.data;
      })
    }
  }
}
</script>
<style scoped>
.order {
  background: #f1f1f1;
}
</style>