<template>
    <briup-fulllayout title="">
        <div>
            {{form}}
            <van-cell-group>
                <van-field v-model="form.telephone" placeholder=""></van-field>
            </van-cell-group>
            <van-cell-group>
                <van-field v-model="form.province" placeholder=""></van-field>
            </van-cell-group>
            <van-cell-group>
                <van-field v-model="form.city" placeholder=""></van-field>
            </van-cell-group>
            <van-cell-group>
                <van-field v-model="form.area" placeholder=""></van-field>
            </van-cell-group>
            <van-cell-group>
                <van-field v-model="form.address" placeholder=""></van-field>
            </van-cell-group>
            <van-button type='primary' block @click="submitHandler"></van-button>
        </div>
    </briup-fulllayout>
</template>

<script>
import {mapState} from 'vuex'
import {get,post} from '../../../http/axios'
export default {
    data(){
        return{
            form:{}
        }
    },
    computed:{
        ...mapState('user',['info'])
    },
    methods:{
        submitHandler(){
            this.form.customerId = this.info.id
            post('/address/saveOrUpdate',this.form).then((res)=>{
                this.$router.go(-1)
                this.$toast.success(res)
            })
        }
    },
    created(){

    }
}
</script>