<template>
    <div>
        <loading v-show="LOADING"></loading>

        <div class="header">
            <div class="logo"></div>
            <div class="title">
                
                <span style="font-size:.8em"></span>
            </div>
        </div>
    <div class="login" v-if="activeTab=='home'">
         <div v-show="isActive" class="form" v-html="content">
             
         </div>
        <div v-show="!isActive" class="form">
            <van-form validate-first @failed="onFailed" @submit="loginHandler">

            <van-cell-group>

                <van-field
                        v-model="form.code"
                        readonly
                        clearable
                        label=""
                        icon="question-o"
                        placeholder=""
                />
                <van-field
                        v-model="form.product"
                       readonly
                        clearable
                        label=""
                        icon="question-o"
                        placeholder=""
                />
                <van-field
                        v-model="form.name"
                        required
                        clearable
                        label=""
                        icon="question-o"
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field
                        v-model="form.phone"
                        required
                        clearable
                        label=""
                        icon="question-o"
                        type="tel"
                        placeholder=""
                        :rules="[{ required: true, message: '' }]"
                />
                <van-field name="uploader" label="">
                <template #input>
                    <van-uploader     @oversize="onOversize"  :max-size="2 * 1024 * 1024"  v-model="fileList" :before-read="beforeRead" :after-read="afterRead" />
                </template>
                </van-field>
                <van-field
                        readonly
                        clickable
                        name="datetimePicker"
                        :value="form.buydate"
                        label=""
                        placeholder=""
                        @click="showPicker = true"
                />
                <van-popup v-model="showPicker" position="bottom">
                    <van-datetime-picker
                            type="date"
                            @confirm="onConfirm"
                            @cancel="showPicker = false"
                            :min-date="minDate"
                            :max-date="maxDate"
                    />
                </van-popup>

               
                <van-field  required
                        v-model="form.addressdetail" label=""
                           :rules="[{ required: true, message: '' }]"/>


            </van-cell-group>
            <div style="text-align:center;padding:1em">
                <van-button round block type="info"  native-type="submit">
                    
                </van-button>

            </div>
            </van-form>

            </div>
        </div>
        <div v-if="activeTab=='search'">
            search
        </div>
         <div v-if="activeTab=='setting'" v-html="content">
           
        </div>
        <div style="padding-top:60px" v-show="!isActive">
        <van-tabbar v-model="activeTab">
        <van-tabbar-item name="home" icon="home-o"></van-tabbar-item>
       
        <van-tabbar-item name="setting" icon="setting-o"></van-tabbar-item>
        </van-tabbar>
        </div>
    </div>
</template>
<script>
     import store from '../store';

    import areaList from "../utils/area.js";
    import {getUrlKey} from "../utils/auth"
    import miment from 'miment'
    import {get,post_json,post} from '../http/axios'
    import { Dialog,Toast } from 'vant';
    import Loading from '../components/Loading.vue'

    import {mapActions} from 'vuex'
    import DirectusSDK from "@directus/sdk-js";
    const api="http://0.0.0.0:9999/"
    const client = new DirectusSDK({
        url: api,
        project: "gcms",
        storage: window.localStorage
    });
   
    export default {
        components: {
          Loading,
        },
        data() {
            return {
                isActive:false,
                activeTab:"home",
                areaList,
                content:"",
                value: '',
                showPicker: false,
                valueArea: "",
                showArea: false,
                fileList:[],
                form: {
                    buydate:  miment().format('YYYY-MM-DD').toString(),
                    address:"",
                    addressdetail:"",
                    product:'',
                    attach:0
                },
                LOADING:true,


                minDate: new Date( 2020, 1, 1),
                maxDate: new Date(2050, 1, 1),
                //currentDate: new Date()
            }
        },
        created: async function () {
            let code =  getUrlKey('code')
            let pid =  getUrlKey('pid')
            this.form.code=code
            let that=this; 
            await client.login({ 
                    email: "baoge@baoge.com",
                    password: "Zaqxsw123!!@#"
            });

            client.getItems("active", {
                filter: {
                    code: {
                    eq: code
                    }
                }
            }).then(data=>{
               //this.$store.commit('hideLoading')
              // this.$store.user.commit('updateLoading', false)
                that.LOADING=false;

                if(data.data.length>0){
                     Dialog({ message: '' });
                     that.isActive=true;
                }

                 console.log(data.data,data.data.length)
            });
            client.getItems("products", {
                filter: {
                    id: {
                    eq: pid
                    }
                }
            }).then(data=>{
                 this.LOADING=false;
                that.content=data.data[0].content
                this.form.product=data.data[0].name
                console.log(that.form.product)
            });
             // this.$store.user.commit('hideLoading')
           this.LOADING=false;

           
            console.log('a is: ' + code)
        },
        methods: {
            ...mapActions("user", ["active"]),
            loginHandler() {

                if(this.form.attach==0){
                     //Notify({ type: 'warning', message: '' });
                     Dialog({ message: '' });
                    return false;
                }
                client.createItem("active",this.form).then(e=>{
                     Dialog({ message: '' });
                      this.isActive=true;
                })

                //  post_json("/gcms/custom/active",this.form)
                //     .then((r)=>{
                //         Dialog({ message: '' });

                //     }).catch((e)=>{


                //  });


            },
            onConfirm(time) {
                this.form.buydate = miment(time).format('YYYY-MM-DD').toString();
                this.showPicker = false;
            },

            onConfirmArea(values) {
                this.form.address = values.map((item) => item.name).join('/');
                this.showArea = false;
            },
            onFailed(errorInfo) {
                console.log('failed', errorInfo);
            },
             beforeRead(file) {
                 
                    if (file.type.indexOf("image") ==-1) {
                        Toast(' jpg  png ');
                        return false;
                    }
                    return true;
              },
              onOversize(file) {
                Toast('2m');
             },
             afterRead(file) {
                let that=this
                // 
                let formData = new FormData()
                formData.append('file', file.file) 

                client.uploadFiles(formData).then(file=>{
                    console.log(file)
                    that.form.attach=file.data.id
                }) 

                
            },
             
        }
    }
</script>
