import {get,post_json,post} from '../../http/axios'
import {setToken,getToken,removeToken} from '../../utils/auth'
export default {
  namespaced:true,
  state: {
    token:getToken(),
    info:{}, //
    LOADING: true, 
  },
  mutations: {
    refreshInfo(state,info){
      state.info = info;
    },
    refreshToken(state,token){
      state.token = token;
    },
    
    updateLoading (state, showLoading) {
      state.LOADING = showLoading
    }

  },
  actions: {
    // 
    async login(context,payload){
      // 1.
         let token = response.data.token;
      // 2.tokenlocalStorage
      setToken(token);
      // 3. token
      context.commit("refreshToken",token);
    },
    // tokeninfo
    async active(context,payload){

      let response = await post_json("/gcms/custom/active",payload);
      alert(response)
      console.log(response);
    },
    // 
    async logout(context){
      // 1. 
      await post('/user/logout');
      // 2. 
      removeToken();
      // 3. store
      context.commit('refreshToken','');
      context.commit('refreshInfo',{});
    }
  }
}